@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap);
html {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-family: 'Roboto Condensed', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  background-color:#792a7f;
  color: #ffffff;
  height: 100%;
}

a:hover {
  color: #ffffff;
  text-decoration: underline;
}

body > div#root {
  height: 100%;
}

body > div#root > div.App {
  min-height: 100%;
}

body > div#root > div.App > main {
  padding-top: 85px;
  position: absolute;
  width: 100%;
  top: 0;
  bottom: 0;
}

main > .container > a {
  display: inline-block;
  position: absolute;
  bottom: 15px;
  right: 15px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.btn-primary {
  color:#fff;
  background-color:#ea733b;
  border-color:#ea733b;
}

.btn-primary:hover {
  color: #fff;
  background-color:#0069d9;
  border-color:#0062cc;
}

.btn-primary:hover {
  color: #fff;
  background-color:#ea733b;
  border-color:#c96537;
}

.btn-primary:active {
  color: #fff;
  background-color:#ea733b;
  border-color:#c96537;
}  

.btn-primary.focus, .btn-primary:focus {
  color: #fff;
  background-color:#ea733b;
  border-color:#c96537;
  box-shadow: 0 0 0 .2rem rgba(255, 38, 226, 0.514);
}

.btn-primary:not(:disabled):not(.disabled).active:focus, .btn-primary:not(:disabled):not(.disabled):active:focus, .show > .btn-primary.dropdown-toggle:focus {
  box-shadow: 0 0 0 .2rem rgba(255, 38, 226, 0.514);
}

.btn-primary:not(:disabled):not(.disabled).active, .btn-primary:not(:disabled):not(.disabled):active, .show > .btn-primary.dropdown-toggle {
  color: #fff;
  background-color:#ea733b;
  border-color:#c96537;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /* background-color: #282c34; */
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  position: relative;
  z-index: 2;
}

.App-header > nav > ul {
  list-style: none;
  padding: 0;
}

.root-component .table-container {
  padding-bottom: 180px;
}

.root-component .icon-container {
  position: relative;
}

.root-component table {
  width: 100%;
  text-align: center;
  color: #ffffff;
}

.root-component table tbody tr:last-child td div.form-group {
  text-align: center;
}

.root-component table tbody tr:last-child td:first-child div.form-group {
  min-width: 58px;
}

.root-component table tbody tr:last-child td input {
  display: inline-block;
}

.root-component table tbody tr:last-child td:first-child input {
  max-width: 58px;
}

.root-component table tbody tr:last-child td:nth-child(2) input {
  max-width: 58px;
}

.root-component table tbody tr:last-child td:nth-child(3) input {
  max-width: 58px;
}

.root-component table tbody tr:last-child td:nth-child(4) input {
  min-width: 170px;
}

.root-component table tbody tr:last-child td:nth-child(5) input {
  min-width: 58px;
}

.root-component table tbody tr:last-child td:nth-child(6) input {
  min-width: 58px;
}

.root-component table tbody tr:last-child td:nth-child(7) input {
  max-width: 58px;
}

.root-component table tbody tr:last-child td:nth-child(8) input {
  max-width: 58px;
}

.root-component table tbody tr:last-child td:nth-child(9) input {
  max-width: 58px;
}

.root-component table tbody tr:last-child td:nth-child(10) input {
  min-width: 58px;
}

.root-component table tbody tr:last-child td:nth-child(11) input {
  min-width: 58px;
}

.App-link {
  /* color: #61dafb; */
  color: #ffffff;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.modal-content {
  background-color: #792a7f;
}

.text-muted {
  color:#ebeff2 !important;
}

